.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(10px);
}

.modal-content {
    background-color: #ffffff;
    margin: 10% auto;
    padding: 20px;
    border-radius: 12px;
    width: 50%;
    transition: all 0.3s ease;
}

.close {
    color: #555;
    float: right;
    font-size: 24px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

.course-button {
    background-color: #001c27;
    color: white;
    padding: 14px 28px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    border-radius: 8px;
    margin: 10px 0;
    transition: background-color 0.3s ease;
}

.course-button:hover {
    background-color: #2d9cdb;
}

.added-courses {
    margin-top: 20px;
    padding: 20px;
    border-radius: 8px;
    background-color: #f5f5f5;
}

.course-box {
    border-radius: 8px;
    padding: 20px;
    margin-bottom: 15px;
    background-color: #ffffff;
    transition: box-shadow 0.3s ease;
}

.course-link {
    color: #001c27;
    text-decoration: none;
    font-size: 18px;
    transition: color 0.3s ease;
}

.course-link:hover {
    color: #2d9cdb;
}

.modal h2 {
    margin-top: 0;
    font-size: 24px;
    color: #001c27;
}

.course-list-item {
    list-style: none;
    padding: 20px;
    margin: 15px 0;
    border-radius: 8px;
    background-color: #f5f5f5;
    transition: box-shadow 0.3s ease;
}

.course-list-item a {
    color: #001c27;
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    transition: color 0.3s ease;
}

.course-list-item a:hover {
    color: #2d9cdb;
}

.profile-circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #2d9cdb;
    color: white;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    text-decoration: none;
    transition: background-color 0.3s ease;
}

.profile-circle:hover {
    background-color: #001c27;
}

@media (max-width: 768px) {
    .modal-content {
        width: 90%;
        padding: 15px;
        margin: 20% auto;
    }

    .course-button {
        padding: 12px 24px;
        font-size: 14px;
    }

    .added-courses {
        padding: 15px;
    }

    .course-box {
        padding: 15px;
    }

    .course-link {
        font-size: 16px;
    }

    .modal h2 {
        font-size: 20px;
    }

    .course-list-item {
        padding: 15px;
        margin: 10px 0;
    }

    .course-list-item a {
        font-size: 16px;
    }

    .profile-circle {
        width: 40px;
        height: 40px;
        font-size: 18px;
    }
}
